<template>
  <div>
    <div class="back">
      <div flex>
        <el-upload
          class="upload-demo"
          action="http://localhost:8082/import/classroom"
          multiple
          :limit="1"
        >
          <el-button size="medium" type="primary" class="bt"
            >上传教室信息</el-button
          >
        </el-upload>
        <div style="margin: 30px"></div>
        <el-upload
          class="upload-demo"
          action="http://localhost:8082/import/student"
          multiple
          :limit="1"
        >
          <el-button size="medium" type="primary" class="bt"
            >上传学生、考场、老师信息</el-button
          >
        </el-upload>
      </div>
      <div style="margin: 100px"></div>
      <el-divider></el-divider>
      <div style="margin: 100px"></div>
      <el-button
        type="primary"
        style="margin-right: 10px"
        size="medium"
        v-loading.fullscreen.lock="fullscreenLoading"
        @click="arrangeFirst"
        class="bt"
        >安排考场</el-button
      >
      <span style="margin: 30px"></span>
      <el-select
        v-model="arrangeType"
        placeholder="请安排座次"
        style="width: 300px"
        @change="eArrangeType"
      >
        <el-option
          v-for="item in types"
          :key="item.id"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <div style="margin: 30px"></div>
    </div>
  </div>
</template>
<script>
import { ArrangeStudent, ArrangeFirst, SettingRecord } from "../api";
export default {
  data() {
    return {
      exams: [],
      classes: [],
      selectedClasses: [],
      students: [],
      arrangeType: "SNO",
      examId: "",
      classNum: "",
      fullscreenLoading: false,
      tableData: [],
      classrooms: [],
      examInfo: "",
      setting: [],
      types: [
        {
          id: 1,
          label: "学号乱序",
          value: "SNO",
        },
        {
          id: 2,
          label: "姓名乱序",
          value: "NAME",
        },
      ],
    };
  },

  watch: {
    async arrangeType(val) {},
  },

  mounted() {
    this.getSetting();
  },
  methods: {
    clean() {
      this.exams = [];
      this.classes = [];
      this.selectedClasses = [];
      this.students = [];
      this.tableData = [];
      this.classrooms = [];
      this.examInfo = {};
      this.examId = "";
      this.classNum = "";
    },
    async getSetting() {
      this.setting = await SettingRecord();
      if (this.setting && this.setting.length > 0) {
        this.arrangeType = this.setting[this.setting.length - 1].type;
      }
    },
    async eArrangeType(val) {
      this.clean();
      this.fullscreenLoading = true;
      let res = await ArrangeStudent({ arrangeType: val });
      if (res) {
        this.$message({
          message: "排座成功",
          type: "success",
        });
      } else {
        this.$message({
          message: "排座失败",
          type: "error",
        });
      }
      this.fullscreenLoading = false;
    },

    async arrangeFirst() {
      this.clean();
      this.fullscreenLoading = true;
      let res = await ArrangeFirst({
        arrangeType: this.arrangeType,
      });
      if (res) {
        this.$message({
          message: "安排考场成功",
          type: "success",
        });
      } else {
        this.$message({
          message: "安排考场失败",
          type: "error",
        });
      }
      this.fullscreenLoading = false;
    },
  },
};
</script>
