<template>
  <div class="examCt">
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :span-method="objectSpanMethod"
    >
      <el-table-column prop="index" label="序号" width="50"> </el-table-column>
      <el-table-column prop="name" label="考试内容" width="180">
      </el-table-column>
      <el-table-column prop="time" label="考试时间" width="300">
      </el-table-column>
      <el-table-column prop="classroom" label="考场" width="200">
      </el-table-column>
      <el-table-column prop="classNum" label="考试班级"> </el-table-column>
      <el-table-column prop="teachName" label="监考老师" width="100">
      </el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button @click="detail(scope.row)" type="text" size="small"
            >详情</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { GetExamsAndClasses, Search, GetExamsInfo } from "../api";
import { DateFormate } from "../utils/DateUtils.js";
export default {
  data() {
    return {
      spanArr: [],
      exams: [],
      classes: [],
      selectedClasses: [],
      students: [],
      arrangeType: "SNO",
      examId: "",
      classNum: "",
      fullscreenLoading: false,
      tableData: [],
      classrooms: [],
      examInfo: "",
      types: [
        {
          id: 1,
          label: "学号乱序",
          value: "SNO",
        },
        {
          id: 2,
          label: "姓名乱序",
          value: "NAME",
        },
      ],
    };
  },
  props: {},
  components: {},
  async created() {
    await this.setInfo();
  },
  watch: {
    examId(val) {
      this.classNum = "";
      this.selectedClasses = [];
      this.classes.forEach((x) => {
        if (x.examId == val) {
          this.selectedClasses.push(x);
        }
      });
    },
  },
  methods: {
    setSpanArr() {
      let spanArr = [];
      let position = 0;
      this.tableData.forEach((item, index) => {
        if (index === 0) {
          spanArr.push(1);
          position = 0;
        } else {
          if (this.tableData[index].id === this.tableData[index - 1].id) {
            spanArr[position] += 1;
            spanArr.push(0);
          } else {
            spanArr.push(1);
            position = index;
          }
        }
      });
      return spanArr;
    },
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 1 || columnIndex === 0) {
        const _row = this.spanArr[rowIndex];
        const _col = _row > 0 ? 1 : 0;
        return {
          rowspan: _row,
          colspan: _col,
        };
      }
    },
    clean() {
      this.exams = [];
      this.classes = [];
      this.selectedClasses = [];
      this.students = [];
      this.tableData = [];
      this.classrooms = [];
      this.examInfo = {};
      this.examId = "";
      this.classNum = "";
    },
    timeTxt(start, end) {
      let fmt = "YYYY-mm-dd HH:MM";
      let fmt2 = "HH:MM";
      return (
        DateFormate(new Date(start), fmt) +
        "~" +
        DateFormate(new Date(end), fmt2)
      );
    },
    async setInfo() {
      let res = await GetExamsInfo();
      res.forEach((item) => {
        item.time = this.timeTxt(item.startTime, item.endTime);
      });
      this.tableData = res;
      let index = 1;
      this.tableData.forEach((item, i) => {
        if (i == 0) {
          item.index = index;
        } else {
          if (this.tableData[i].name == this.tableData[i - 1].name) {
            item.index = index;
          } else {
            index++;
            item.index = index;
          }
        }
      });

      this.spanArr = this.setSpanArr();
    },

    detail(data) {
      this.$router.push({
        name: "Seat",
        query: data,
      });
    },

    async search() {
      if (!this.examId || !this.classNum || !this.arrangeType) {
        this.$message({
          message: "请先选择必选项",
          type: "error",
        });
        return;
      }
      let res = await Search({
        examId: this.examId,
        classNum: this.classNum,
        arrangeType: this.arrangeType,
      });
      this.examInfo = res;
      this.classrooms = res.classrooms;
      this.tableData = [];
      for (let index = 0; index < this.classrooms.length; index++) {
        let classroomDTO = this.classrooms[index];
        this.tableData = this.tableData.concat(classroomDTO.studentDTOs);
      }
      if (this.arrangeType == "SNO") {
        this.tableData.sort(function (a, b) {
          var x = a.id.toLowerCase();
          var y = b.id.toLowerCase();
          if (x < y) {
            return -1;
          }
          if (x > y) {
            return 1;
          }
          return 0;
        });
      } else {
        this.tableData.sort(function (a, b) {
          var x = a.name.toLowerCase();
          var y = b.name.toLowerCase();
          if (x < y) {
            return -1;
          }
          if (x > y) {
            return 1;
          }
          return 0;
        });
      }
    },
  },
};
</script>

<style scoped lang="scss">
.examCt {
  margin: 100px;
}
</style>
