<template>
  <div class="container">
    <div
      style="
        background-color: #a2a2a2;
        height: 30px;
        text-align: center;
        color: white;
      "
      flex
    >
      <div>
        <img src="../assets/notice.png" class="img-class" alt="" />
      </div>
      <span style="margin-top: 7px; margin-left: 30px" class="animate"
        >诚信考试 杜绝作弊
      </span>
    </div>

    <el-form label-width="200px" :inline="true">
      <el-form-item label="">
        <el-button type="txt" @click="drawer = true" style="padding-top: 10px"
          >查看座位号</el-button
        >
      </el-form-item>
      <el-form-item label="考试科目：">
        <span>{{ examName }}</span>
      </el-form-item>
      <el-form-item label="监考老师：">
        <span>{{ teachName }}</span>
      </el-form-item>
      <el-form-item label="教室：">
        <span>{{ classroom }}</span>
      </el-form-item>
      <el-form-item label="考试起止时间：">
        <span>{{ time }}</span>
      </el-form-item>
    </el-form>

    <el-drawer title="" :visible.sync="drawer" direction="ltr">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column type="index" align="center" label="序号" width="70">
        </el-table-column>
        <el-table-column prop="name" align="center" label="姓名"> </el-table-column>
        <el-table-column prop="id" label="学号" width="130"> </el-table-column>
        <el-table-column prop="classNum" label="班级" width="130"> </el-table-column>
        <el-table-column prop="seat" align="center" label="座位号" width="80">
        </el-table-column>
      </el-table>
    </el-drawer>
    <div
      style="
        background-color: #1c86ee;
        height: 50px;
        text-align: center;
        letter-spacing: 100px;
        width: 100%;
      "
    >
      <h1 style="color: white; width: 100%">座位表</h1>
    </div>
    <div style="margin-left: 450px">
      <div
        class="rectangle"
        style="margin-top: 10px;padding-top:10px; margin-left: 250px; display: block;font-size:18px;background-color:#5a9bd5"
      >
        讲台
      </div>

      <div style="margin: 10px"></div>
      <div v-for="i in parseInt(classroomNum / 8 + 1)" :key="i">
        <span class="rectangle" v-for="j in 4" :key="j">
          <span class="item" style="float: left; margin-left: 10px;">{{
            parseInt(classroomNum / 8 + 1) * (2 * j - 2) + i
          }}</span>
          <span class="item" style="float: right; margin-right: 10px;">{{
            parseInt(classroomNum / 8 + 1) * (2 * j - 1) + i
          }}</span>
        </span>
      </div>
      <el-divider></el-divider>
    </div>
  </div>
</template>

<script>
import { GetExamsAndClasses, Search } from "../api";
import { DateFormate } from "../utils/DateUtils.js";
export default {
  data() {
    return {
      exams: [],
      classes: [],
      selectedClasses: [],
      students: [],
      arrangeType: "SNO",
      examId: "",
      classNum: "",
      fullscreenLoading: false,

      classrooms: [],
      examInfo: "",
      types: [
        {
          id: 1,
          label: "学号乱序",
          value: "SNO",
        },
        {
          id: 2,
          label: "姓名乱序",
          value: "NAME",
        },
      ],

      relationId: "",
      tableData: [],
      classroomNum: "",
      teachName: "",
      time: "",
      examName: "",
      drawer: false,
      classroom: "",
    };
  },
  props: {},
  components: {},
  mounted() {
    this.relationId = this.$route.query.relationId;
    this.getStudents();
    this.classroom = this.$route.query.classroom;
    this.classroomNum = this.$route.query.classroomNum;
    this.teachName = this.$route.query.teachName;
    this.examName = this.$route.query.name;
    this.time = this.$route.query.time;
  },
  methods: {
    async getStudents() {
      if (!this.relationId) {
        this.$message({
          message: "relationId 为空",
          type: "error",
        });
        return;
      }
      let res = await Search({
        relationId: this.relationId,
      });
      this.tableData = res;
    },
  },
};
</script>

<style scoped lang="scss">
.img-class {
  width: 20px;
  height: 20px;
  margin-left: 10px;
  margin-top: 5px;
}
.animate {
  padding-left: 20px;
  display: inline-block;
  white-space: nowrap;
  animation: 10s wordsLoop linear infinite normal;
}
@keyframes wordsLoop {
  100% {
    transform: translateX(100%);
    -webkit-transform: translateX(100%);
  }
  0% {
    transform: translateX(10%);
    -webkit-transform: translateX(10%);
  }
}

@-webkit-keyframes wordsLoop {
  100% {
    transform: translateX(100%);
    -webkit-transform: translateX(100%);
  }
  0% {
    transform: translateX(10%);
    -webkit-transform: translateX(10%);
  }
}
/deep/ .el-drawer__open .el-drawer.ltr {
  overflow: auto !important;
}
/deep/ .el-form-item {
  margin-bottom: 0px;
}
.rectangle {
  height: 40px;
  width: 120px;
  display: inline-block;
  line-height: 30px;
  text-align: center;
  border-radius: 4px;
  color: #ffffff;
  font-size: 12px;
  margin-right: 50px;
  margin-bottom: 20px;
  background-color: #1c86ee;
}
.item{
  padding-top:5px;
  font-size:14px
}
</style>
