import Vue from 'vue'
import Router from 'vue-router'
import Back from "../components/back.vue"
import Exam from "../components/exam.vue"
import Seat from "../components/seat.vue"
Vue.use(Router)

export default new Router({
  mode: 'hash', // 去掉url中的#
  routes: [
    {
      path: '/back',
      name: 'Back',
      component: Back
    }, {
      path: '/exam',
      name: 'Exam',
      component: Exam
    },
    , {
      path: '/seat',
      name: 'Seat',
      component: Seat
    }
  ]
})