<template>
  <div class="login-wrap">
    <el-form class="login-container" label-width="80px">
      <h1 class="title">用户登录</h1>
      <el-form-item label="账号">
        <el-input type="text" v-model="account"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password" v-model="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width: 100%" @click="doSubmit()"
          >登陆</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { Login } from "../api.js";
export default {
  data() {
    return {
      account: "admin",
      password: "admin",
    };
  },
  props: {},
  components: {},
  activated() {},
  watch: {},
  methods: {
    async doSubmit() {
      let res = await Login({
        account: this.account,
        password: this.password,
      });
      if (!res) {
        alert("账号或者密码错误");
        return;
      }
      this.$emit("login");
      if (res.role == "admin") {
        this.$router.push({
          name: "Back",
        });
      } else {
        this.$router.push({
          name: "Exam",
        });
      }
    },
  },
};
</script>

<style scoped lang="scss">
.login-wrap{
  margin: 200px 600px auto 600px;
}
.title{
  margin-left:150px
}
</style>
